.banner {
  padding: 40px 0px;
}

.banner__container {
  width: 320px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
  background-color: #f4efe9;
  border-radius: 20px;
  flex-direction: column;
  .banner__container__img {
    display: none;
  }
  @media only screen and (min-width: 768px) {
    flex-direction: row;
    width: 768px;
    height: 300px;
    .banner__container__img {
      width: 100%;
      height: auto;
      display: block;
    }
    & img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  @media only screen and (min-width: 1200px) {
    width: 1255px;
    .banner__container__img {
      width: initial;
      height: auto;
      display: block;
      justify-content: flex-end;
    }
    & img {
      width: initial;
      height: initial;
      display: initial;
    }
  }
}
